import React from 'react';
import {NavLink} from 'react-router-dom';

const NavBarTemporary = () => {
  return (
    <header>
      <div className="nav-bar-temporary">
        <div className="container">
          <nav className="nav d-flex">
            <NavLink className="itemTemporary" to="/"></NavLink>
            <NavLink className="itemTemporary" to="/search_tickets"> </NavLink>
            <NavLink className="itemTemporary" to="/seat_selection"> </NavLink>
            <NavLink className="itemTemporary" to="/passengers"></NavLink>
            <NavLink className="itemTemporary" to="/pay_selection"> </NavLink>
            <NavLink className="itemTemporary" to="/check_confirm_order"> </NavLink>
            <NavLink className="itemTemporary" to="/order_confirmed"></NavLink>
          </nav>
        </div>
      </div>
    </header>
  );
};

export default NavBarTemporary;